@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic&display=swap);
.popupwrapper{
	margin-top:10%;
	width:100%;
	min-height:100%;
	position:fixed;
	z-index:400;
	position:absolute;
	 background-color: rgba(0,0,0,0.4); 
	top:0;
	left:0;
	overflow:auto;
}

.store-popup{
	width:100%;
	min-height:100%;
	
	position:absolute;
	top:0;
	left:0;
	background:white;
}

.popupimg{
	width : 90%;
	height: 60%;
}

.btndiv{
	background:#ee7047;
	width:100%;
	height: 40px;
	padding-top:5px;
}

.popup-remove-btn{
	width : 15.5%;
	height: 100%;
	border : 0px;
	background : #ee7047;
	outline :none;
	float:right;
	font-size:16px;
	color:white;
}

.popup-content{
	padding-left:15px;
}

.popupinfo tr,td{
	width:50%;
}

.floornav {
	border : 1px solid #444;
	display : table;
	width : 100%;
	background : white;
	height: 40px;
	table-layout: fixed;
	border-collapse : collapse;
	margin-bottom:10px;
}

.floorlink{
	display : table-cell;
	vertical-align : middle;
	border : 1px solid #444;
	text-align : center;
	border-collapse : collapse;
	-webkit-tap-highlight-color : transparent;
}

.floorlink a{
	color : #444;
}

.selected{
	color : white;
	background :#ee7047;
}

.listoreimg{
	width : 90%;
	height : 80px;
	margin-bottom : 0;
}

.storename{
	font-size:20px;
}

.listinfotext{
	background : #dcdcdc;
}

.subinfo p{
	margin : 0 auto 10px 0;
}

#lastsub{
	margin-bottom:0;
}

.storename{
	margin-top : 0;
	padding-top:5px;
}
.store{
	display : inline-block;
	width : 42.5%;
	margin-left:5%;
	margin-bottom:5%;
	border : 1px solid #444;
	text-align:center;
}

.storelist {
	padding: 0;
}



.categorynavelem, .selected{
	display : table-cell;
	vertical-align : middle;
	border : 1px solid #444;
	text-align : center;
	border-collapse : collapse;
	color:#444;
	-webkit-tap-highlight-color : transparent;
}

.map {
	margin : 30px 5%;
	
}

.map img{
	
}



.categorynav{
	
	border : 1px solid black;
	display : table;
	width : 100%;
	background : white;
	height: 40px;
	table-layout: fixed;
	border-collapse : collapse;
}

.selected{
	color : white;
	background :#ee7047;
}

.map img, .map a, .map canvas{
	-webkit-tap-highlight-color : transparent;
	outline:none;
}
.search{
	width : 90%;
	height: 40px;
	border : 1px solid #ee7047;
	background : white;
	margin:0 auto;
}

.search input{
	width : 73.125%;
	padding: 12px 10px 8px 10px;
	border :0px;
	outline:none;
	float:left;
	font-size : 16px;
}

.search button{
	width : 15.5%;
	height: 100%;
	border : 0px;
	background : #ee7047;
	outline :none;
	float:right;
	font-size:16px;
	color:white;
}
.categoryheadnav{
	border : 1px solid black;
	display : table;
	width : 100%;
	background : white;
	height: 40px;
	table-layout: fixed;
	border-collapse : collapse;
	margin-bottom:20px;
}
.headernav {
	border : 1px solid #444;
	display : table;
	width : 100%;
	background : white;
	height: 40px;
	table-layout: fixed;
	border-collapse : collapse;
	margin-bottom:15px;
}

.link, .selected{
	display : table-cell;
	vertical-align : middle;
	border : 1px solid #444;
	text-align : center;
	-webkit-tap-highlight-color : transparent;
	border-collapse : collapse;
}

.link a,.link a:visited{
	color : #444;
}

.selected{
	background :#ee7047;
}

.selected a,.selected a:visited{
	color :white;
}





header a h2{
	text-decoration:none;
	padding-left:10px;	
	color:black;
}


.title{
	-webkit-tap-highlight-color : transparent;
}
*{
	box-sizing:border-box;
}

html, body ,root {
	margin : 0 auto;
	padding: 0;
	-webkit-text-size-adjust: none;
	min-width : 320px;
	
}

.container{
	width : 100%;	
	font-family: 'Nanum Gothic', sans-serif;
	position:relative;
}


a {
	text-decoration : none;
}

input:focus{
	outline:none;
}

